import React, { useRef } from 'react';
// @mui
import { Avatar, Box, Popover, useTheme } from '@mui/material';
import IconButton from '@mui/material/IconButton';
import OutlinedInput from '@mui/material/OutlinedInput';
import InputAdornment from '@mui/material/InputAdornment';
import FormControl from '@mui/material/FormControl';
import { Add } from '@mui/icons-material';
import Card from '@mui/material/Card';
import CardHeader from '@mui/material/CardHeader';
import CardContent from '@mui/material/CardContent';
import CardActions from '@mui/material/CardActions';
import Typography from '@mui/material/Typography';
import MenuItem from '@mui/material/MenuItem';
import MoreVertIcon from '@mui/icons-material/MoreVert';

// third-party
import { useDrop } from 'react-dnd';

// project
import { ItemTypes } from './types';
import Task from './Task';

// assets
import { faker } from '@faker-js/faker';
import { sample, sampleSize } from 'lodash';

const NEW_TASK = {
  id: faker.datatype.uuid(),
  title: faker.lorem.paragraph(),
  description: faker.lorem.paragraph(),
  author: {
    name: faker.internet.userName(),
    avatarUrl: faker.image.avatar()
  },
  assignedTo: [...Array(Math.round(Math.random() * 10) + 1)].map(
    (_, index) => ({
      name: faker.internet.userName(),
      avatarUrl: faker.image.avatar()
    })
  ),
  Attachments: [...Array(Math.round(Math.random() * 10) + 1)].map((_, index) =>
    faker.image.image()
  ),
  due: faker.date.future(),
  labels:
    // [...Array(Math.round(Math.random())+1)].map((_, index) => (
    sampleSize(['Design', 'Code', 'Test'], 2),
  //  ))
  checklists: [...Array(Math.round(Math.random()) + 1)].map((_, index) => ({
    user: faker.internet.userName(),
    title: faker.lorem.paragraph(),
    createdAt: faker.date.past()
  })),
  comments: [...Array(Math.round(Math.random()) + 1)].map((_, index) => ({
    id: faker.datatype.uuid(),
    //   title: COMMMENTS[index + 1],
    comment: faker.lorem.paragraph(),
    author: {
      name: faker.internet.userName(),
      avatarUrl: faker.image.avatar()
    },
    createdAt: String(faker.date.past())
  })),
  view: faker.datatype.number(),
  comment: faker.datatype.number(),
  share: faker.datatype.number(),
  favorite: faker.datatype.number(),
  createdAt: faker.date.past(),
  status: sample(['todo', 'progress', 'done'])
};

const options = [
  'Remove',
  // 'Rename',
  'Clear'
];

// const ITEM_HEIGHT = 45;

const MyMenu = (props) => {
  const { status, columns, editColumns, tasks, editTasks } = props;
  const [anchorEl, setAnchorEl] = React.useState(null);

  const handleOpen = (event) => {
    setAnchorEl(event.currentTarget);
  };
  const handleMenuClose = (option) => {
    switch (option?.trim()?.toLowerCase()) {
      case 'remove':
        const newColumns = columns.filter((column) => column !== status);
        editColumns(newColumns);
        break;

      case 'rename':
        // columns = columns.filter(column=> column !== status)
        break;
      case 'clear':
        const newTasks = tasks.filter((task) => task.status !== status);
        editTasks(newTasks);
        break;
      default:
        break;
    }
    setAnchorEl(null);
  };
  const handleClose = () => {
    setAnchorEl(null);
  };
  const open = Boolean(anchorEl);
  const id = open ? 'simple-popover' : undefined;
  return (
    <>
      <IconButton aria-label="more" aria-describedby={id} onClick={handleOpen}>
        <MoreVertIcon />
      </IconButton>
      <Popover
        id={id}
        open={open}
        anchorEl={anchorEl}
        onClose={handleClose}
        anchorOrigin={{
          vertical: 'bottom',
          horizontal: 'left'
        }}
      >
        {options.map((option) => (
          <MenuItem
            key={option}
            selected={option === 'remove'}
            onClick={() => handleMenuClose(option)}
          >
            {/* <Edit /> */}
            {option}
          </MenuItem>
        ))}
      </Popover>
    </>
  );
};

const Column = (props) => {
  const { status, tasks, onDrop, handleNewTask } = props;
  const theme = useTheme();
  const [{ isOver }, drop] = useDrop({
    accept: ItemTypes.CARD,
    drop: (item) => onDrop(item, status),
    collect: (monitor) => ({
      isOver: monitor.isOver()
    })
  });

  const tasksInColumn = tasks.filter((t) => t.status === status);
  const newTask = useRef(null);

  let color = '';
  switch (status) {
    case 'todo':
      color = 'primary';
      break;
    case 'progress':
      color = 'warning';
      break;
    case 'done':
      color = 'success';
      break;
    default:
      color = 'primary';
      break;
  }

  const handleAddTask = () => {
    handleNewTask({ ...NEW_TASK, title: newTask.current.value, status });
    newTask.current.value = '';
  };
  return (
    <Card sx={{
      width: '100%',
      maxWidth: 336,
      minWidth: 200,
      mb: 5,
      bgcolor: theme.palette.mode === 'light' ? theme.palette.grey[100] : theme.palette.grey[700]
    }}>
      <CardHeader
        avatar={
          <Avatar
            sx={{
              borderRadius: '50%',
              borderBottomRightRadius: 0,
              bgcolor: `${color}.main`
            }}
          >
            {tasksInColumn.length}
          </Avatar>
        }
        action={<MyMenu {...props} />}
        title={
          <Typography
            color="text.primary"
            variant="h5"
            sx={{ color: theme.palette.text.primary }}
          >
            {status?.toUpperCase()}
          </Typography>
        }
      />

      <CardContent>
        <Box
          ref={drop}
          sx={{
            flex: 1,
            width: 1,
            minHeight: 100,

            // margin: 1,
            // padding: 1,
            backgroundColor: isOver
              ? theme.palette.primary[100]
              : theme.palette.background.white
          }}
        >
          {tasksInColumn.map((task) => (
            <Task key={task.id} task={task} />
          ))}
        </Box>
      </CardContent>
      <CardActions disableSpacing>
        <FormControl sx={{ width: '100%' }} variant="outlined">
          <OutlinedInput
            id="newtask"
            inputRef={newTask}
            type="text"
            fullWidth
            placeholder="Please enter new task"
            endAdornment={
              <InputAdornment position="end">
                <IconButton
                  aria-label="visibility"
                  onClick={handleAddTask}
                  // onMouseDown={handleMouseDownPassword}
                  edge="end"
                  color="primary"
                >
                  <Add />
                </IconButton>
              </InputAdornment>
            }
          />
        </FormControl>
      </CardActions>
    </Card>
  );
};

export default Column;
