import React from 'react';

// @mui
import {
  AvatarGroup,
  Avatar,
  Box,
  Chip,
  Typography,
  useTheme
} from '@mui/material';

// third-party
import { useDrag } from 'react-dnd';

// project
import MainCard from 'components/cards/MainCard';
import { ItemTypes } from './types';
import TaskAdd from './TaskAdd';

const Task = ({ task }) => {
  const theme = useTheme();
  const [{ isDragging }, drag] = useDrag({
    type: ItemTypes.CARD,
    item: { ...task },
    collect: (monitor) => ({
      isDragging: monitor.isDragging()
    })
  });

  const opacity = isDragging ? 0.5 : 1;
  // const backgroundColor = "white";
  // const border = "1px solid black";

  const [open, setOpen] = React.useState(false);
  const handleTaskDrawerOpen = (event) => {
    // console.log(event.type);
    // console.log(event);
    if (
      event.type === 'keydown' &&
      (event.key === 'Tab' || event.key === 'Shift')
    ) {
      return;
    }
    setOpen(!open);
  };

  return (
    <MainCard sx={{ my: 2 }}>
      <Box ref={drag} sx={{ opacity }} onClick={handleTaskDrawerOpen}>
        <Typography variant="body1" gutterBottom>
          {task.title?.substring(0, 100)}
          {task.title?.length > 100 ? '...' : undefined}
        </Typography>
        {task?.labels?.map((item, index) => (
          <Chip
            key={index}
            label={item}
            size="small"
            color="info"
            sx={{ mr: 1, borderRadius: `${theme.shape.borderRadius}px` }}
          />
        ))}
        <Box
          sx={{
            display: 'flex',
            justifyContent: 'flex-start',
            flexWrap: 'nowrap',
            mt: 2
          }}
        >
          <AvatarGroup max={6}>
            <Avatar src={task?.author?.avatarUrl} sx={{ mr: 3 }} />
            {task?.assignedTo?.map((usr, index) => (
              <Avatar key={index} alt={usr?.name} src={usr?.avatarUrl} />
            ))}
          </AvatarGroup>
          {/* {task.checklists.length > 0 ? <IconListCheck /> : ''}
                    {task.comments.length > 0 ? <IconMessage /> : ''} */}
          <TaskAdd
            task={task}
            open={open}
            toggleDrawer={handleTaskDrawerOpen}
          />
        </Box>
      </Box>
    </MainCard>
  );
};

export default Task;
